<template>
  <div class="Chart">
    <header><i class="iconfont icon-xiangzuo1" @click="toIndex"></i><span></span></header>
    <div class="container">

       <div id="main" style="width: 500px;height:400px;"></div>
       <!--  -->

    </div>
  </div>
</template>
<script type="text/javascript">
  
  import echarts from "echarts"

  export default{
    name:"Chart",
    data(){
      return{ 
      }
    },
    mounted(){

        var neData = ["", "KA", "", "", "", "","KA"];
        var neValue = [8044.5, 2728.059, 1829.445, 6.58, 0, 249.257,18.706];
        var data = [];

        for(var i = 0;i< neData.length ; i++){
            data[i]={
            name:"",
            value:""
          };

          data[i].name = neData[i];
          data[i].value = neValue[i];
        }

        // domecharts
        var myChart = echarts.init(document.getElementById('main'));

        // 
        var option = {
            title: {
                text: ''
            },
            //,
            tooltip: {
               trigger: 'item',
               formatter: "{a} <br/>{b}: {c} ({d}%)"// {a}{b}{c}{d}
            },
            //
            legend: {
                //
                orient: 'horizontal',
                y: 'bottom',  
                bottom:200,
                //dataseries-data
                data:["", "KA", "", "", "", "","KA"]
            },
            series: [{
              name: '',
              type: 'pie',
              data: data,
              radius : ["26%", '45%'],
              center: ['60%', '50%'],
              markPoint:{
                symbol:"triangle"
              },
              label: {
                normal: {
                  show: true, 
                  position: 'right',
                  formatter: "{b} : {d}%",// {a}{b}{c}{d}{d}value

                  textStyle : {                   
                    align : 'center',
                    baseline : 'middle',
                    fontFamily : '',
                    fontSize : 15,
                    fontWeight : 'bolder'
                  }
                },
              },

            }]
        };
        // 
        myChart.setOption(option);  
    },
    created () {
    }
  }
</script>
<style scoped lang="less">
  .Chart{
    .container{
      width: 100%;
      div{
         margin-top: 180/50rem;
         canvas{
         }
      }
     
    }
  }
</style>


