<template>
	<div class="ReplenishTaskList">
		<header><i class="iconfont icon-xiangzuo1" @click="toArea"></i><span></span></header>
		<div class="container">
			<table class="tableContainer" cellspacing="0">
				<thead class="tableHead">
					<tr>
						<td @click="SortArr('Replenish_TaskData','ToLoc','SortCondition1',$event)"></td>
						<td @click="SortArr('Replenish_TaskData','Sku','SortCondition2',$event)"></td>
						<td @click="SortArr('Replenish_TaskData','FromLoc','SortCondition3',$event)"></td>
						<td @click="SortArr('Replenish_TaskData','Qty','SortCondition4',$event)"></td>
						<td @click="SortArr('Replenish_TaskData','Status','SortCondition5',$event)"></td>
					</tr>
				</thead> 
				<tbody>
					<tr v-for='(dataItem,index) in Replenish_TaskData'   @click="selectTask(index)"   :class="{ 'selectedTask':index==detail_item,'normalTask':index!=detail_item}"  :id="index"  :key='index' >
						<td>{{dataItem.ToLoc}}</td>
						<td>{{dataItem.Sku}}</td>
						<td>{{dataItem.FromLoc}}</td>
						<td>{{dataItem.Qty}}</td>
						<td>{{dataItem.Status}}</td>
					</tr>
				</tbody>
			</table>
		</div>
		<div class="bottomNum">
			<div class="taskNum">
				<div>
					<span></span>
					<input  readonly  v-model="tasksNum"></input>
				</div>
				<div>
					<span></span>
					<input  readonly  v-model="LibraryNum"></input>
				</div>
			</div>
			<div class="g_doubleBtn">
				<div  @click="AbolishAllTasks"></div>
				<div  @click="Replenishment"></div>
			</div>
		</div>
		<div v-if="tipsHide"  @touchmove.prevent  class="g_tips">
			<div>
				<span></span>
				<span>{{msg}}</span>
				<p  @click='tipsErrorHide(inputFocus,ifRouter)'></p>
			</div>
		</div>
	</div>
</template>
<script type="text/javascript">
	export default{
		name:"ReplenishTaskList",
		data(){
			return{
				tipsHide:false,//
				msg:"",//
				tasksNum:"",//
				LibraryNum:"",//
				Replenish_TaskData:[],//
				detail_item:0,//
				ifRouter:false,//
				inputFocus:"",
				ifChangeStatus:false,//
				SortCondition1:false,
				SortCondition2:false,
				SortCondition3:false,
				SortCondition4:false,
				SortCondition5:false,
				SortCondition6:false,
			}
		},
		mounted(){
			$("thead").find("td").append('<i class="iconfont"></i>')
		},
		created(){

			var that = this;

			if(this.$store.state.Replenish_TaskData.length){

				var Replenish_TaskData = this.$store.state.Replenish_TaskData;

				this.Replenish_TaskData = Replenish_TaskData;

				this.tasksNum = Replenish_TaskData.length;

				// 
				if(this.$route.query.ifChangeStatus){

					this.ifChangeStatus = true;

				}else{
					this.ifChangeStatus = false;
				}

				console.log(this.ifChangeStatus)
				console.log(that.$store.state.Replenish_StatusArr)

				var RecordNum = 0;

				for(var i = 0 ; i < Replenish_TaskData.length ; i++){

					// 
					if(that.ifChangeStatus){

						Replenish_TaskData[i].Status = that.$store.state.Replenish_StatusArr[i];

					}else{

						that.$store.state.Replenish_StatusArr[i] = Replenish_TaskData[i].Status;
					}

					// 
					if(i < Replenish_TaskData.length-2){

						if(Replenish_TaskData[i].ToLoc == Replenish_TaskData[i+1].ToLoc){

							RecordNum++;
						}
					}
				}

				// 
				this.LibraryNum = Replenish_TaskData.length - RecordNum;
			}
		},
		methods:{
			//
			tipsErrorHide:function (input,ifRouter){

				this.tipsHide = false ;

				if(ifRouter){
					//   
					this.$router.push({
						name:"ReplenishArea",
						query:{
						}
					})
				}

				if(input){

					this.$refs[input].select();
				}
			},
			// 
			toArea:function(){

				// / 
				this.$router.push({
					name:"ReplenishArea",
					query:{
					}
				})
			},
			// 
			Replenishment:function(){

				var that = this;

				var ReplenishIndex = this.detail_item;

				if(this.Replenish_TaskData[that.detail_item].Status==9){

					that.tipsShow();

					that.msg = "";
					return;
				}
				// 
				this.$router.push({
					name:"Replenishment",
					query:{
						ReplenishIndex:ReplenishIndex
					}
				})
			},
			// 
			AbolishAllTasks:function(){

				var that = this;

				var AbolishTasksUrl = this.HOST + "RFRepDelTask.do";

				var AbolishTasksData = {
					TaskGroup:that.Replenish_TaskData[0].TaskGroup,
				}

				this.httpRequest(AbolishTasksUrl,AbolishTasksData,function(res){

					that.Replenish_TaskData = [];

					that.tipsShow();

					that.msg = "";

					that.ifRouter = true;//
				})
			},
			// 
			selectTask:function(index){

				this.detail_item = index;
			}
		}
	}
			// .tableHead{
			// 	position: fixed;
			// 	top:90/50rem;
			// 	left:0;
			// 	background-color: #fff;
			// 	z-index: 6;
			// }

			// margin-top: 50/50rem;
</script>

<style lang="less" scoped>
	.ReplenishTaskList{
		.container{
			margin-bottom: 150/50rem;
			.tableContainer{
				width: 100%;
				padding-bottom: 100/50rem;
			}
			tr{
				td{
					min-width: 80/50rem;
					padding:0 10/50rem;
					height: 65/50rem;
					margin:0;
					text-align: center;
	
				}
				td:nth-child(1){
					border-left: none;
				}
			}
			.selectedTask{
				background-color: #eee;
			}
			
		}
		.bottomNum{
		    font-size: 29/50rem;
			width: 100%;
			height:150/50rem;
			position: fixed;
			left: 0;
			bottom: 0;
			background-color: #fff;
			z-index: 5;
			&>div.taskNum{
				overflow: hidden;
				&>div{
					text-align: center;
					float: left;
					width: 375/50rem;
					height: 60/50rem;
					span{
						height: 60/50rem;
						margin-right:10/50rem;
					}
					input{
						width: 160/50rem;
						text-align: center;
						height: 45/50rem;
						font-size: 29/50rem;
						background-color:#f0f0f0;
						outline:none;
						border:none;
					}
				}
			}
			&>div.g_doubleBtn{
				margin-top: 0;
			}
			
		}
	}
	@media screen and (max-width: 330px){
		.ReplenishTaskList{
			.container{
				tr{
					td{
						min-width: 200/50rem;
					}
					td:nth-child(4),td:nth-child(5){
						min-width: 130/50rem;
					}
				}
			}
			.bottomNum{
				font-size: 38/50rem;
				&>div.taskNum{
					&>div{
						input{
							font-size: 38/50rem;
						}
					}
				}
			}
		}
	}
	@media screen and (max-width: 250px){
		.ReplenishTaskList{
			.container{
				tr{
					td{
						height: 105/50rem;
						min-width: 260/50rem;
					}
					td:nth-child(4),td:nth-child(5){
						min-width: 160/50rem;
					}
				}
			}
			.bottomNum{
				font-size: 42/50rem;
				&>div.taskNum{
					&>div{
						input{
							font-size: 42/50rem;
						}
					}
				}
			}
		}
	}
</style>


