import Vue from 'vue'
import Router from 'vue-router'

// 
import Login from "@/pages/Login/login" 

//
import Index from '@/pages/index'

//
import InventoryQuery from '@/pages/InventoryQuery/InventoryQuery'    //
import InventoryDetails from '@/pages/InventoryQuery/InventoryDetails'   //
import InventoryList from '@/pages/InventoryQuery/InventoryList'   //

//
import Putaway from '@/pages/Putaway/Putaway'
import PutawayDetails from '@/pages/Putaway/PutawayDetails'

//
import Receipt from "@/pages/Receipt/Receipt"   //
import ScanCommodityCode from "@/pages/Receipt/ScanCommodityCode"   //
import BindingCommodityCode from "@/pages/Receipt/BindingCommodityCode"   //
import TakeDelivery from "@/pages/Receipt/TakeDelivery"   // 
import Summary from "@/pages/Receipt/Summary"     //
import SelectOrder from "@/pages/Receipt/SelectOrder"   //



//
import CycleCount from '@/pages/CycleCount/CycleCount'
import CycleCount_ChoiceNo from '@/pages/CycleCount/CycleCount_ChoiceNo'
import CycleCount_Submit from '@/pages/CycleCount/CycleCount_Submit'

// 
import RFPick from '@/pages/RFPick/RFPick'  //
import RFPick_select from "@/pages/RFPick/RFPick_select"  // 
import RFPick_confirm from "@/pages/RFPick/RFPick_confirm"  // 

// 
import RFInvMove from '@/pages/RFInvMove/RFInvMove'  //

// 
import ConsignmentIndex from "@/pages/ConsignmentNumCollect/ConsignmentIndex"    // 
import ConsignmentNumCollect from "@/pages/ConsignmentNumCollect/ConsignmentNumCollect"   //
import CheckDetails from "@/pages/ConsignmentNumCollect/checkDetails"   //  
import ChoiceGoods from "@/pages/ConsignmentNumCollect/ChoiceGoods"   //  

// 
import RFOrderShip from "@/pages/RFOrderShip/RFOrderShip" //
import RFOrderDetailQuery from "@/pages/RFOrderShip/RFOrderDetailQuery"  // 

// 
import ReplenishIndex from "@/pages/Replenishment/ReplenishIndex" //
import ReplenishArea from "@/pages/Replenishment/ReplenishArea" //-/
import ReplenishTaskList from "@/pages/Replenishment/ReplenishTaskList" //-
import Replenishment from "@/pages/Replenishment/Replenishment" //

// ()
import Chart from "@/pages/chart/chart" //


// 
import Binding from "@/pages/Login/Binding" // openid


//
import SkuBound from '@/pages/SkuBound/SkuBound'
import StoreKey_select from '@/pages/SkuBound/StoreKey_select'
import Sku_select from '@/pages/SkuBound/Sku_select'

//
//
import CheckOrder from '@/pages/check/CheckOrder'
import CheckOrder_check from '@/pages/check/CheckOrder_check'
import CheckOrder_detail from '@/pages/check/CheckOrder_detail'

//
import CheckLoad from '@/pages/check/CheckLoad'
import CheckLoad_check from '@/pages/check/CheckLoad_check'
import CheckLoad_detail from '@/pages/check/CheckLoad_detail'

Vue.use(Router)

export default new Router({
  routes: [
    // 
    {
      path: '/',
      name: 'Login',
      component: Login
    },
    // 
    {
      path: '/Index',
      name: 'Index',
      component: Index
    },
    // 
    {
      path: '/InventoryQuery',
      name: 'InventoryQuery',
      component: InventoryQuery
    },
    {
      path:'/InventoryDetails',
      component: InventoryDetails,
      name: 'InventoryDetails',
    },
    {
      path:'/InventoryList',
      component: InventoryList,
      name: 'InventoryList',
    },
    // 
    {
      path: '/Putaway',
      name: 'Putaway',
      component: Putaway
    },
    {
      path:'/PutawayDetails',
      component: PutawayDetails,
      name: 'PutawayDetails',
    },
    // 
    {
      path:'/Receipt',
      component: Receipt,
      name: 'Receipt',
    },
    {
      path:'/ScanCommodityCode',
      component: ScanCommodityCode,
      name: 'ScanCommodityCode',
    },
    {
      path:'/BindingCommodityCode',
      component: BindingCommodityCode,
      name: 'BindingCommodityCode',
    },
    {
      path:'/TakeDelivery',
      component: TakeDelivery,
      name: 'TakeDelivery',
    },
    {
      path:'/Summary',
      component: Summary,
      name: 'Summary',
    },
    {
      path:'/SelectOrder',
      component: SelectOrder,
      name: 'SelectOrder',
    },
    // 
    {
      path:'/CycleCount',
      component: CycleCount,
      name: 'CycleCount',
    },
    {
      path:'/CycleCount_ChoiceNo',
      component: CycleCount_ChoiceNo,
      name: 'CycleCount_ChoiceNo',
    },
    {
      path:'/CycleCount_Submit',
      component: CycleCount_Submit,
      name: 'CycleCount_Submit',
    },
    //
    {
      path:'/RFPick',
      component: RFPick,
      name: 'RFPick',
    },
    {
      path:"/RFPick_select",
      component: RFPick_select,
      name: 'RFPick_select',
    },
    {
      path:"/RFPick_confirm",
      component: RFPick_confirm,
      name: 'RFPick_confirm'
    },
    //
    {
      path:'/RFInvMove',
      component: RFInvMove,
      name: 'RFInvMove',
    },
    // 
    {
      path:'/ConsignmentIndex',
      component: ConsignmentIndex,
      name: 'ConsignmentIndex',
    },
    {
      path:'/ConsignmentNumCollect',
      component: ConsignmentNumCollect,
      name: 'ConsignmentNumCollect',
    },
    {
      path:'/CheckDetails',
      component: CheckDetails,
      name: 'CheckDetails',
    },
    {
      path:'/ChoiceGoods',
      component: ChoiceGoods,
      name: 'ChoiceGoods',
    },
    // 
    {
      path:'/RFOrderShip',
      component: RFOrderShip,
      name: 'RFOrderShip',
    },
    {
      path:'/RFOrderDetailQuery',
      component: RFOrderDetailQuery,
      name: 'RFOrderDetailQuery',
    },
    // 
    {
      path:'/ReplenishIndex',
      component: ReplenishIndex,
      name: 'ReplenishIndex',
    },
    {
      path:'/ReplenishArea',
      component: ReplenishArea,
      name: 'ReplenishArea',
    },
    {
      path:'/ReplenishTaskList',
      component: ReplenishTaskList,
      name: 'ReplenishTaskList',
    },
    {
      path:'/Replenishment',
      component: Replenishment,
      name: 'Replenishment',
    },
    {
      path:'/Binding',
      component: Binding,
      name: 'Binding',
    },
    // 
    {
      path:'/SkuBound',
      component: SkuBound,
      name: 'SkuBound',
    },
    {
      path:'/StoreKey_select',
      component: StoreKey_select,
      name: 'StoreKey_select',
    },
    {
      path:'/Sku_select',
      component: Sku_select,
      name: 'Sku_select',
    },
    // 
    {
      path:'/Chart',
      component: Chart,
      name: 'Chart',
    },
    //
       {
         path:'/CheckOrder',
         component:CheckOrder,
         name:'CheckOrder',
       },
       {
       	path:'/CheckOrder_check',
       	component:CheckOrder_check,
       	name:'CheckOrder_check'
       },
       {
       	path:'/CheckOrder_detail',
       	component:CheckOrder_detail,
       	name:'CheckOrder_detail'
       },
    //
        {
         path:'/CheckLoad',
         component:CheckLoad,
         name:'CheckLoad',
       },
       {
       	path:'/CheckLoad_check',
       	component:CheckLoad_check,
       	name:'CheckLoad_check'
       },
       {
       	path:'/CheckLoad_detail',
       	component:CheckLoad_detail,
       	name:'CheckLoad_detail'
       },
  ]
})



